.ColorPicker svg{
  position: relative;
  left: .25rem;
}

.colorInput{
  display: none;
}

.deleteIconButton:hover{
  color: #c70b0be6 !important;
}

.stateClassTooLate{
  color: #e41616f0 !important;
  font-weight: 500 !important;
}

.timeSetterError{
  box-shadow: 0px 0px 10px .5px #ec0b0bf2;
  padding: 12.5px 0;
  border-radius: 6px;
}

#addProjectRow .ColorPicker svg{
  position: relative;
  left: 1.575rem;
  top: 0.2rem;
}

#addProjectLabelDiv label{
  bottom: .7rem;
}

#addProjectRow .setNameCell, #addProjectRow .setDurationCell{
  padding-top: 1rem !important;
}

@media(max-width: 820px){
  #addProjectRow td:last-child{
    text-align: right;
  }
}

#ProjectsTable{
  min-width: 100vw;
  position: relative;
  right: 1rem;
}

.setNameCell input{
  width: 6rem;
}

.setDurationCell input{
  width: 5rem;
}

.setStartTimeCell{
  padding-top: 1rem !important;
}

.setStartTimeLabelDiv{
  position: absolute;
  height: 0;
  width: 0;
}

.setStartTimeCell label{
  position: relative;
  width: 10rem;
  bottom: .8rem;
  color: var(--label);
}

.BreaksDrawer tr:last-child td{
  padding-top: 1.5rem;
  padding-bottom: 0.2rem;
}

.BreaksDrawer tr:nth-last-child(2) td{
  border-bottom-color: #4848488c;
}

.defaultProjectsDrawerRemoveTransform > div {
  -webkit-transform: none !important;
          transform: none !important;
}

.signInPanel{
  position: absolute;
  bottom: 2.5rem;
  width: calc(100% - 2rem);
  background-image: radial-gradient(#67bfaf4d, #5ac3c363);
}

.signInPanel, .signInPanelContainer{
  min-height: 5rem;
  border-radius: 1rem;
}

.signInPanelContainer{
  background-image: radial-gradient(#ffffff40, #ffffff59);
  padding: .5rem;
}

.signInPanel .dismissButton{
  position: absolute;
  top: -.5rem;
  right: -.5rem;
  background-color: rgba(0, 0, 0, 0) !important;
}

.projectsTable{
  table-layout: fixed;
  white-space: nowrap;
}

.projectsTable th:first-child,
.projectsTable td:first-child{
  width: 3rem;
  padding: 0 0 0 1rem;
}

.dragIcon{
  cursor: -webkit-grab;
  cursor: grab;
  color: var(--drag-icon);
}

.timeStatsLabelDiv, #addProjectLabelDiv{
  position: absolute;
}

.timeStatsLabelDiv label, #addProjectLabelDiv label{
  position: relative;
  width: 10rem;
  color: var(--label);
  font-size: .8125rem;
  font-weight: 400;
}

/* planning */
#setEndTime{
  margin: 2rem 0;
}

#setEndTime .TimeSetterDesktop label{
  margin-right: 1rem
}

#setEndTime .TimeSetterDesktop input,
#setEndTime .TimeSetterMobile input{
  text-align: center;
  margin: 0 .1rem;
}

#setEndTime .TimeSetterDesktop input{
  width: 4rem;
}

#setEndTime .TimeSetterMobile input{
  width: 5rem;
}

.setStartTimeCell input, #BreaksDrawer .TimeSetterDesktop input{
  width: 4rem;
  text-align: center;
  margin: 0 .1rem;
}

#resetButton:hover{
  background-color: #c70b0be6 !important;
  color: white;
}

.planningSecondaryButton:hover{
  background-color: rgba(3, 155, 229, 0.08) !important;
}

.drawerTitle{
  padding: .5rem;
}

.BreaksView .title,
.DefaultProjectsView .drawerTitle{
  width: 100%;
  text-align: center;
}

